<template>
	<view class="box">
		<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback">
			<template v-for="(item,k) in data_list">
				<view class="article-box" @tap="chooseArticle(k)" :key="k">
					<view class="time">
						{{item.issuedDate}}
					</view>
					<view class="article-cont">
						<image lazy-load class="img" :src="item.articlePic" mode="aspectFill"></image>
						<view class="right">
							<view class="title">{{item.articleTitle}}</view>
							<view class="name">
								{{item.customerName}}
							</view>
						</view>
					</view>
				</view>
			</template>
		</mescroll-body>
		<view class="blank" :class="{'padd-safe':shapedScreen}"></view>
		<view class="fix-box" :class="{'padd-safe':shapedScreen}">
			<navigator url="/pages/spread/advertise" class="bg-btn">
				添加新文章
			</navigator>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				shapedScreen:this.shapedScreen
			}
		},
		components: {
			MescrollBody
		},
		methods: {
			chooseArticle(k){
				this.$store.commit('saveIssueArticle',{
					articleTitle:this.data_list[k].articleTitle,
					articleSeqId:this.data_list[k].seqId,
				});
				uni.navigateBack({})
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oMyArticle/findArticlePage', {
					params: {
						articleStatus: "INIT",
						pageno: pageNum,
						pagesize: pageSize,
						issuedBySeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
		}
	}
</script>

<style lang="scss">

	.bg-btn {
		width: calc(100% - 200rpx);
		margin: 0 auto;
	}
	.slide_tab {
		overflow: hidden;
		width: 100%;
		height: 90rpx;
	}

	.article-box {
		width: 660rpx;
		padding: 20rpx 25rpx;
		background: rgba(255, 255, 255, 1);
		border-radius: 10rpx;
		margin: 10rpx auto;

		.time {
			font-size: 26rpx;
			margin-bottom: 16rpx;
			color: #666666;
		}

		.img {
			width: 200rpx;
			height: 180rpx;
			flex-shrink: 0;
			margin-right: 40rpx;
			border-radius: 6rpx;
			background: #eee;
		}

		.article-cont {
			display: flex;
			align-content: center;

			.title {
				font-size: 30rpx;
				line-height: 42rpx;
				width: 100%;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.name {
				font-size: 26rpx;
				color: #999999;
				width: 100%;
			}

			.icon {
				display: flex;
				justify-content: flex-end;
				width: 100%;

				.icon-li {
					display: flex;
					align-items: center;
					font-size: 26rpx;
					margin-right: 36rpx;

					.iconfont {
						font-size: 24rpx;
						margin-right: 10rpx;
					}
				}

				.icon-li:last-child {
					margin: 0;
				}
			}

			.right {
				display: flex;
				align-content: space-between;
				flex-wrap: wrap;
				flex-grow: 1;
			}
		}
	}

	.font-red {
		color: #FF5F51;
	}

	.font-blue {
		color: #4872E5;
	}
</style>
